.profile__post {
    padding: 10px;
    background-color: rgb(25, 177, 25);
    margin-bottom: 1em;
}

.profile__post:last-child {
    margin: 0;
}

.profile__post_user_and_post_info {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 1em;
    border-bottom: 1px solid green;
    margin-bottom: 1em;
}

.profile__post_link {
    text-decoration: none;
    color: black;
}

.profile__post_userinfo {
    display: flex;
    align-items: center;
}

.profile__post_userpic {
    border-radius: 50%;
    height: 100%;
    display: block;
    width: 40px;
    margin-right: 0.5em;
}

.profile__post_content {
    margin-bottom: 1em;
}