/* dialogs */

.dialogs {
    display: flex;
}

.dialogs__users_column {
    flex-basis: 25%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: green;
    margin-right: 10px;
}

.dialogs__messages_column {
    flex-basis: 75%;
}

.dialogs__user_link {
    color: white;
}

.dialogs__user_active_link {
    color: black;
}