.message {
    display: flex;
    align-items: center;
    padding: 10px;
    background-color: green;
    border-radius: 10px;
    margin-bottom: 10px;
}

.message:last-child {
    margin: 0;
}

.message__userpic {
    border-radius: 50px;
    width: 50px;
    height: 50px;
}

.message__content {
    margin-left: 10px;
    border-radius: 10px;
    background-color: lightcoral;
    padding: 10px;
}