.header {
    padding-left: 10px;
    grid-area: h;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    align-items: center;
    background-color: rgb(88, 186, 219);
}

.logo {
    height: 100%;
}

.search {
    border: none;
    border-radius: 10px;
    padding: 5px 15px;
    outline: none;
    background-color: rgb(108, 166, 185);
}

.search::placeholder {
    color: white;
}

.search:focus {
    background-color: #fff;
    color: black;
}

.settings {
    padding: 10px 20px 10px 10px;
    display: flex;
    align-items: center;
    position: relative;
}

.settings__item {
    margin: 0 5px;
}

.settings__userpic {
    display: block;
    height: 50px;
}

.settings::after {
    position: absolute;
    top: 50%;
    right: 5%;
    content: '';
    border: 5px solid transparent;
    border-top: 5px solid black;
}

.settings:hover {
    background-color: rgb(108, 166, 185);
    cursor: pointer;
}