* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

a {
    text-decoration: none;
}

body {
    background-color: lightblue;
}

.app {
    display: grid;
    grid-template-rows: auto 1fr;
    grid-template-columns: 2fr 10fr;
    grid-template-areas:
        'h h'
        'n c';
    grid-gap: 10px;
    padding: 10px 0;
    max-width: 1000px;
    margin: 0 auto;
}

.app__central_content {
    grid-area: c;
}